export default {
	data() {
		return {
			title:'',
			dateStr:'',
			teacherName:"",
			content:"",
			imgUrl:'../../static/circularbead@3x.png',
			id:'',
		};
	},
	onLoad(o){
		this.id = o.id;
	},
	onShow(){
		this.getinfo();
	},
	methods:{
		getinfo(){
			uni.request({
				url: this.$api+'/weixin/shishenxin/homeworkmxdetaile',
				data: this.getmd5({
					id:this.id
				}),
				header: {
					'content-type': 'application/json'
				},
				method: "POST",
				success: (res) => {
					if (res.data.code != 200) {
						uni.showToast({
						icon: 'none',
						title: res.data.msg,
						duration: 2000
						});
					} else {
						this.list = [];
						uni.hideLoading();
						var data = res.data.data;
						this.title = data.title;
						this.dateStr = data.dateStr;
						this.teacherName = data.teacherName;
						this.content = data.content;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
					icon: 'none',
					title: '获取失败',
					duration: 2000
					});
				}
			});
		},
		previewImage: function() {
			uni.previewImage({
				urls: [this.img]
			})
		},
	}
}
